import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import './setupMocks'; // Add mocks at the top
import { initWidget } from './lib/widget';

// Dynamically set Webpack's public path if `widget` is available
if (window.widget && window.widget.uwaUrl) {
  __webpack_public_path__ = window.widget.uwaUrl.substring(0, window.widget.uwaUrl.lastIndexOf("/") + 1);
}


function start() {
  const root = ReactDOM.createRoot(document.getElementById('root'));
  root.render(
    <React.StrictMode>
      <App />
    </React.StrictMode>
  );
}

export function initializeWidget() {
  initWidget((widget) => {
    widget.addEvent("onLoad", () => {
      console.log("Widget Loaded");
      start();
    });

    widget.addEvent("onRefresh", () => {
      console.log("Widget Refreshed");
    });
  });
}

// Measure performance in your app (optional)
reportWebVitals();
